import { Component, OnInit } from '@angular/core';
import {HunkRichTextComponent} from  '../../components/hunk-richtexteditor/hunkRichTexteditor.component';
import {APIService} from  '../../globals/services/API.service';
import {MessageTypes,ErrorService,ErrorMsg} from  '../../globals/services/error.service';
import {HtmlcontentsService} from  '../../globals/services/htmlcontents.service';
import { HunkToolbarComponentComponent } from '../../components/hunk-toolbar-component/hunk-toolbar-component.component';
import { Hunkhtml} from '../../directives/hunkhtml.directive';
import { CkeditorComponent, } from '../../components/ckeditor/ckeditor.component';
@Component({
  moduleId: module.id,
  selector: 'app-kapcsolat',
  templateUrl: 'kapcsolat.component.html',
  styleUrls: ['kapcsolat.component.css'],
  directives: [HunkRichTextComponent,Hunkhtml,CkeditorComponent,HunkToolbarComponentComponent],
  providers: [APIService]
})
export class KapcsolatComponent {
  toolbar1show:boolean=false;
  editor1show:boolean=false;
  toolbar2show:boolean=false;
  editor2show:boolean=false;
  content:any={name:'',phone:'', email:'',comments:''}
  constructor(public _HtmlcontentsService:HtmlcontentsService,
              private _APIService:APIService,
              private _ErrorService:ErrorService) {
  }
  onToolbar1Leave(event){
    this.toolbar1show=false;
  }
  onToolbar2Leave(event){
    this.toolbar2show=false;
  }
  onChange1View(viewMode){
    this.editor1show=(viewMode=='edit')?true:false;
  }
  onChange2View(viewMode){
    this.editor2show=(viewMode=='edit')?true:false;
  }
  onValueChanged1(content){
    this._HtmlcontentsService.contents.kapcsolat1.content=content;
  }
  onValueChanged2(content){
    this._HtmlcontentsService.contents.kapcsolat2.content=content;
  }
  save1Content(){

   this._HtmlcontentsService.setContent( this._HtmlcontentsService.contents.kapcsolat1.id,this._HtmlcontentsService.contents.kapcsolat1.content);
    this.editor1show=false;
  }
  save2Content(){

    this._HtmlcontentsService.setContent( this._HtmlcontentsService.contents.kapcsolat2.id,this._HtmlcontentsService.contents.kapcsolat2.content);
    this.editor2show=false;
  }
  sendMessage() {


    this._APIService.getResponse('api/kapcsolat/email/', this.content).subscribe(
        data=> {
          this._ErrorService.errorClear();
          if (data.success)
          {
            this.content={name:'',phone:'', email:'',comments:''};
            let error:ErrorMsg = {
              type: MessageTypes.mtSuccess,
              msg: [data.success]
            }
            this._ErrorService.setErrorMsg(error);
          }
        console.log(data);

        if (data.message) {
          let error:ErrorMsg = {
            type: MessageTypes.mtDanger,
            msg: data.message.errors
          }
          this._ErrorService.setErrorMsg(error);
        }




      },
        error=> {
        console.log(error);

      })

  }
}
